<template>
  <demo title="Tabs" class="tabs-demo">
    <demo-item name="Default" description="" :code="code" slot="left">
      <Tabs>
        <TabPane label=""></TabPane>
        <TabPane label=""></TabPane>
        <TabPane label=""></TabPane>
      </Tabs>
    </demo-item>

    <demo-item class="type-group" name="Type" :description="type_descr" :code="type_code" slot="right">
      <Tabs type="no_padding">
        <TabPane label="">padding1</TabPane>
        <TabPane label="">padding2</TabPane>
        <TabPane label="">padding3</TabPane>
      </Tabs>

      <Tabs type="fix_width">
        <TabPane label="">1</TabPane>
        <TabPane label="">2</TabPane>
        <TabPane label="">3</TabPane>
      </Tabs>

      <Tabs type="fix_width:140px">
        <TabPane label="">120px1</TabPane>
        <TabPane label="">120px2</TabPane>
        <TabPane label="">120px3</TabPane>
      </Tabs>
    </demo-item>

    <demo-item name="Icon" description="" :code="icon_code" slot="left">
      <Tabs>
        <TabPane label="Home" icon="home"></TabPane>
        <TabPane label="Cart" icon="shoppingcart"></TabPane>
        <TabPane label="User" icon="user"></TabPane>
        <TabPane label="Setting" icon="setting"></TabPane>
      </Tabs>
    </demo-item>

    <demo-item name="Style" description="" :code="style_code" slot="left">
      <Tabs type="no_padding" class="red-tabs">
        <TabPane label=""></TabPane>
        <TabPane label=""></TabPane>
        <TabPane label=""></TabPane>
      </Tabs>
    </demo-item>
  </demo>
</template>

<style lang="stylus" scoped>
  @import '../../style/';

  .tabs-demo .hiui-tab-pane {
    font-size: 14px;
    color: #999;
  }

  .type-group .hiui-tabs&:not(:last-child) {
    margin-bottom: 40px;
  }

  .red-tabs .hiui-tabs-bar-item.active {
    color: _red;
    border-bottom-color: _red;
  }
</style>

<script>
  export default {
    data () {
      return {
        code: `
<Tabs>
  <TabPane label=""></TabPane>
  <TabPane label=""></TabPane>
  <TabPane label=""></TabPane>
</Tabs>
        `,
        type_code: `
<Tabs type="no_padding">
  <TabPane label="">padding1</TabPane>
  <TabPane label="">padding2</TabPane>
  <TabPane label="">padding3</TabPane>
</Tabs>

<Tabs type="fix_width">
  <TabPane label="">1</TabPane>
  <TabPane label="">2</TabPane>
  <TabPane label="">3</TabPane>
</Tabs>

<Tabs type="fix_width:140px">
  <TabPane label="">120px1</TabPane>
  <TabPane label="">120px2</TabPane>
  <TabPane label="">120px3</TabPane>
</Tabs>
        `,
        icon_code: `
<Tabs>
  <TabPane label="Home" icon="home"></TabPane>
  <TabPane label="Cart" icon="shoppingcart"></TabPane>
  <TabPane label="User" icon="user"></TabPane>
  <TabPane label="Setting" icon="setting"></TabPane>
</Tabs>
        `,
        style_code: `
<Tabs type="no_padding" class="red-tabs">
  <TabPane label=""></TabPane>
  <TabPane label=""></TabPane>
  <TabPane label=""></TabPane>
</Tabs>

<style lang="stylus">
.red-tabs .hiui-tabs-bar-item.active {
  color: _red;
  border-bottom-color: _red;
}
</style>
        `,
        type_descr: `
\`no_padding\` padding\n
\`fix_width\`  \n
\`fix_width:String\`  
        `
      }
    }
  }
</script>



